body {
  background: #000;
}
.instructions {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background: linear-gradient(to right, #131313, #1a1a2e);
  border: 3px solid #fff;
  border-radius: 10px;
  color: #f2f2f2;
  box-shadow: 0 0 20px rgba(133, 97, 211, 0.6);
}
.instructions h2 {
  text-align: center;
  margin-bottom: 15px;
  color: #12B1D1;
  font-size: 20px;
}
.instructions ul {
  list-style-type: disc;
  padding-left: 20px;
}
.instructions li {
  margin-bottom: 8px;
  font-size: 14px;
  line-height: 1.6;
  font-size: 20px;
}
@media (max-width: 768px) {
  .instructions {
    padding: 15px;
  }
  .instructions li {
    font-size: 13px;
  }
}
.card {
  max-width: 800px;
  margin: 40px auto;
  padding: 30px;
  background: linear-gradient(to right, #590392, #030c0e);
  border-radius: 10px;
  border: 5px solid #FFF;
  box-shadow: 0 0 30px rgb(133, 11, 181);
}
table {
  width: 100%;
  border-collapse: collapse;
  background-color: linear-gradient(to right, #ff9900, #077d98);
  border-radius: 10px;
  box-shadow: rgba(137, 97, 211, 0.878) 0px 10px 10px -5px;
}
table td {
  padding: 10px;
  font-size: 14px;
}
label {
  font-weight: bold;
  color: #e8dede;
}
input[type="text"],
input[type="number"],
input[type="date"],
select {
  width: calc(50% - 10px);
  padding: 10px;
  border-radius: 5px;
  border: 2px solid #ccc;
  margin-top: 5px;
  box-sizing: border-box;
  background-color: #f9f9f9;
  box-shadow: rgba(137, 97, 211, 0.878) 0px 10px 10px -5px;
}
input[type="text"]:focus,
input[type="number"]:focus,
input[type="date"]:focus,
select:focus {
  border-color: #12B1D1;
  outline: none;
  box-shadow: rgba(137, 97, 211, 0.878) 0px 10px 10px -5px;
}
input[type="file"] {
  padding: 5px;
  border-radius: 5px;
  border: 2px solid #ccc;
  color: #e8dede;
  box-shadow: rgba(137, 97, 211, 0.878) 0px 10px 10px -5px;
}
button[type="submit"] {
  background: linear-gradient(45deg, rgb(16, 137, 211), rgb(18, 177, 209));
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  margin-top: 15px;
  box-shadow: rgba(133, 189, 215, 0.878) 0px 20px 10px -15px;
  transition: all 0.2s ease-in-out;
}
button[type="submit"]:hover {
  transform: scale(1.03);
  box-shadow: rgba(133, 189, 215, 0.878) 0px 23px 10px -20px;
}
button[type="submit"]:active {
  transform: scale(0.95);
  box-shadow: rgba(133, 189, 215, 0.878) 0px 15px 10px -10px;
}
@media (max-width: 768px) {
  input[type="text"],
  input[type="number"],
  input[type="date"],
  select {
    width: 100%;
  }
  table td {
    padding: 8px;
  }
  button[type="submit"] {
    width: 100%;
  }
}